import React from "react";

import { Link } from "react-router-dom";

import { makeStyles } from "@material-ui/core/styles";
import { List, ListItem } from "@material-ui/core";

import * as path from "../../constants/routes";

const useStyles = makeStyles((theme) => ({
  menuList: {
    display: "flex",
    // marginRight: theme.spacing(2),
    "& li": {
      "& a": {
        position: "relative",
        whiteSpace: "nowrap",
        textDecoration: "none",
        color: "black",
        fontFamily: "Fira Sans",
        textTransform: "uppercase",
        fontWeigth: "normal",
        fontStyle: "normal",
        fontSize: "14px",
      },
    },
    "& li.defaultLink": {
      "& a": {
        "&:before": {
          content: "' '",
          width: "100%",
          height: "1px",
          background: "#35D69B",
          position: "absolute",
          bottom: "-18px",
          left: "0",
          opacity: "0",
          transition: ".2s",
        },
      },
      "&:hover": {
        "&  > a": {
          "&:before": {
            bottom: "-10px",
            left: "0",
            opacity: "1",
            transition: ".2s",
          },
        },
      },
    },
    "& li.activeLink": {
      "&  > a": {
        position: "relative",
        "&:before": {
          content: "' '",
          width: "100%",
          height: "1px",
          background: "#35D69B",
          position: "absolute",
          bottom: "-10px",
          left: "0",
          opacity: "1",
        },
      },
    },
  },
}));

const menuListLink = [
  {
    link: path.HOME,
    name: "Главная",
  },
  {
    link: path.ABOUT_ME,
    name: "Обо мне",
  },
  {
    link: path.PORTFOLIO,
    name: "Портфолио",
  },
  {
    link: path.FAQ,
    name: "FAQ",
  },
  {
    link: path.CONTACTS,
    name: "Контакты",
  },
];

export const NavigationMenu = (props) => {
  const classes = useStyles();
  return (
    <List className={classes.menuList}>
      {menuListLink.map((item, id) => (
        <ListItem
          key={id}
          className={
            props.location.pathname === item.link ? "activeLink" : "defaultLink"
          }
        >
          <Link to={item.link}>{item.name}</Link>
        </ListItem>
      ))}
    </List>
  );
};
